<template>
  <div class="base-video">
    <!--<video id="sports_video" class="video-js" data-setup='{"autoplay": "true", "liveui": "true", "controls": "true", "fluid": "true", "suppressNotSupportedError": ""}' poster="@img/nav/logo.png">-->
    <!--<source id="source" src="https://play.k56t.cn/live/stream50778.m3u8" type="application/x-mpegURL">-->
    <!--</video>-->
    <video id="sports_video" class="video-js" poster="@img/nav/logo.png">
      <source id="source" type="application/x-mpegURL">
    </video>
    <div class="details">
      <div class="item name"><img src="@img/home/football.png" v-if="match.type===1"><img src="@img/home/basketball.png" v-else><span>{{match.name}}</span></div>
      <div class="item time">{{match.matchTime}}</div>
      <div class="item status">{{('1') | interpreter('MatchType')}}</div>
      <div class="item home"><img :src="match.hteam_logo"><span>{{match.hteam_name}}</span></div>
      <div class="item score">{{match.score}}</div>
      <div class="item guest"><img :src="match.ateam_logo"><span>{{match.ateam_name}}</span></div>
      <div class="item channel">
        <div class="video" v-for="(video, k) in match.live_urls" :key="k" @click="$emit('play', {type: match.type, playType: 1, channel: k, id: match.id})"><img src="@img/list/live_video.png"/><span>{{video.name}}</span></div>
        <div class="animation" @click="$emit('play', {type: match.type, playType: 2, channel: 0, id: match.id})"><img src="@img/list/live_animation.png"/><span>动画直播</span></div>
      </div>
    </div>
  </div>
</template>

<script>
import videojs from 'video.js'
import 'video.js/dist/video-js.css'
import 'videojs-landscape-fullscreen'
import zhCN from 'video.js/dist/lang/zh-CN.json'
export default {
  name: 'BaseVideo',
  data () {
    return {
      match: {
        type: 1,
        matchTime: '19:00',
        hteam_logo: 'https://cdn.sportnanoapi.com/football/team/32ea0f2d506ec8181164b8563387f7f0.png',
        ateam_logo: 'https://cdn.sportnanoapi.com/football/team/32ea0f2d506ec8181164b8563387f7f0.png',
        hteam_name: 'A队',
        ateam_name: 'B队',
        live_urls: []
      },
      setup: {
        autoplay: true,
        liveui: true,
        controls: true,
        fluid: true,
        suppressNotSupportedError: false,
        children: [
          'bigPlayButton',
          'controlBar'
        ]
      }
    }
  },
  mounted () {
    // 设置语言
    videojs.addLanguage('zh-CN', zhCN)
    videojs('sports_video', this.setup, function onPlayerReady () {
      videojs.log('Your player is ready!')

      this.on('loadstart', function () {
        console.log('开始请求数据 ')
      })
      this.on('progress', function () {
        console.log('正在请求数据 ')
      })
      this.on('loadedmetadata', function () {
        console.log('获取资源长度完成 ')
      })
      this.on('canplaythrough', function () {
        console.log('视频源数据加载完成')
      })
      this.on('waiting', function () {
        console.log('等待数据')
      })
      this.on('play', function () {
        console.log('视频开始播放')
      })
      this.on('playing', function () {
        console.log('视频播放中')
      })
      this.on('pause', function () {
        console.log('视频暂停播放')
      })
      this.on('ended', function () {
        console.log('视频播放结束')
      })
      this.on('error', function () {
        console.log('加载错误')
      })
      this.on('seeking', function () {
        console.log('视频跳转中')
      })
      this.on('seeked', function () {
        console.log('视频跳转结束')
      })
      this.on('ratechange', function () {
        console.log('播放速率改变')
      })
      this.on('timeupdate', function () {
        console.log('播放时长改变')
      })
      this.on('volumechange', function () {
        console.log('音量改变')
      })
      this.on('stalled', function () {
        console.log('网速异常')
      })
    })
  }
}
</script>

